
<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">德育记录</p>
        </div>
        <div class="item-for mt-84">
            <div class="item-list" v-for="(item,index) in list" :key="index" @click="details(item.id)">
                <img class="item-head l" :src="$api.fileUrl + item.headImage" alt="">
                <div class="item-content l ml-36">
                    <p class="setH setHF f26 c333">[{{item.userName}}] {{item.eval_desc}}</p>
                    <p class="setH f16 c999">{{item.create_time | fileTime(item.create_time) }}</p>
                </div>
                <div class="mathAddOrDown r f24 c333" v-if="item.score_type == 1">{{item.score}}</div>
                <div class="mathAddOrDown r f24 cred" v-else>{{item.score}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            math:[{name:5,active:true},{name:10,active:false},{name:15,active:false}],
            show:true,
            list:[]
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/moraleducation'});//返回上一级
        },
        history(){
            this.$toast.loading({
                mask: true,
                message: '加载中...',
                duration:0
            });
            this.$http({url:this.$api.moraleducation.history},{classesId:JSON.parse(localStorage.getItem('zhxy_user')).classesId}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                }
                this.$toast.clear();
            })
        },
        details(id){
            this.$router.push({path:'/moraleducationdetail',query:{id:id}});//跳转详情
        }

    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num);
        }
    },
    created() {
        this.history();
    },
}
</script>

<style scoped>
    .item-for{position: absolute;top: 0;right: 0;bottom: 0;left: 0;overflow-y: auto}
    .item-list{width: 100%;height: 1.8rem;border-bottom: 1px solid #f2f2f2;}
    .item-list{padding: 0.3rem;width: 100%;box-sizing: border-box;overflow: hidden;}
    .item-head{width: 1.2rem;height: 1.2rem;border-radius: 50%;display: block;}
    .setH{height: 0.6rem;line-height: 0.6rem;padding: 0;margin: 0;}
    .setHF{line-height: 0.8rem}
    .mathAddOrDown{line-height: 1.2rem;}
    .cred{color: red;}
</style>





